/** @jsx jsx */
import React from 'react';
import { LinkToStacked } from 'gatsby-theme-andy/src/components/CustomLinkToStacked';
import { Link } from 'gatsby';
import { jsx } from 'theme-ui';

import Tippy from 'gatsby-theme-andy/src/components/Tippy';

import { ExternalIcon } from './ExternalIcon';

const AnchorTag = ({ href, popups = {}, noPopups = false, ...restProps }) => {
  if (!href) href = restProps.to;
  if (!href.match(/^http/))
    return noPopups ? (
      <Link {...restProps} to={href}/>
    ) : ( <>
      <span sx={{ variant: 'invisibleText' }}>[</span>
      <Tippy content={popups[href.replace(/^\//, '')]} placement="right" animation="shift-away">
        <LinkToStacked {...restProps} to={href} sx={{ variant: 'links.internal' }} />
      </Tippy>
      <span sx={{ variant: 'invisibleText' }}>]</span>
      </>
    );
  return <>
      <a {...restProps} href={href} />
      <ExternalIcon/>
    </>;
};

export default {
  a: AnchorTag,
};
